}

@font-face {
  font-family: "VW Headline OT";
  src: url("https://assets.ctfassets.net/pntshaoi0gaf/6ym6c6hMJioS4w2UmGkKGu/30c9c062963da9d288f821a427cc2ba4/VWHeadlineOT_Semibold.ttf");
}

@font-face {
    font-family: "VW Headline OT Black";
